<template>
	<div>
		<div class="btn">
			<el-button type="primary" icon="el-icon-delete" @click="delSelection()" :disabled="disabledDel">全部删除</el-button>
			<el-button type="primary" @click="dialogMessage = true">添加资讯类型</el-button>
		</div>
		<el-table :data="tableDate" ref="multipleTable" @selection-change="handleSelectionChange" class="aip-table">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="分类ID" align="center"></el-table-column>
			<el-table-column prop="title" label="分类标题" align="center">
			</el-table-column>
			<el-table-column prop="href" label="分类图标" align="center">
				<template slot-scope="scope"><img :src="scope.row.href" style="max-width:180px;max-height:80px;" /></template>
			</el-table-column>
			
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalPage">
			</el-pagination>
		</div>
		
		<el-dialog title="新增资讯类型" :visible.sync="dialogMessage">
			<el-form :model="form" label-width="100px">
				<el-form-item label="资讯分类名">
					<el-input v-model="form.title"></el-input>
				</el-form-item>
				<el-form-item label="资讯分类图标">
					<el-upload action="https://api.fengniaotuangou.cn/api/upload" list-type="picture-card" :before-upload="beforeAvatarUpload"
					 :on-success="handleAvatarSuccess" :on-exceed="handleExceed">
						<i slot="default" class="el-icon-plus"></i>
						<div slot="file" slot-scope="{file}">
							<img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
							<span class="el-upload-list__item-actions">
								<span v-if="!disabled" class="el-upload-list__item-delete" @click="handleRemove(file)">
									<i class="el-icon-delete"></i>
								</span>
							</span>
						</div>
					</el-upload>
				</el-form-item>
			</el-form>
			<el-button type="primary" class="editor-btn" @click='submit'>提交</el-button>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/modules/message.js'
	var file
	export default {
		data() {
			return {
				dialogMessage: false,
				tableDate: [],
				form: {
					title: '',
					href: ''
				},
				disabled: false,
				upLoadImg: '',
				disabledDel: true,
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getMessageType()
		},
		methods: {
			getMessageType() {
				var self = this;
				API.messageTypes(self.currentPage).then(res => {
					self.tableDate = res.data
					self.totalPage = res.total
				})
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
				this.disabledDel = false;
			
				if (this.multipleSelection == '') {
					this.disabledDel = true;
				}
			},
			// 删除多行
			delSelection(rows) {
				if (rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.delSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			handleRemove(file, fileList) { //移除图片
				console.log(file, fileList);
			},
			beforeAvatarUpload(file) { //文件上传之前调用做一些拦截限制
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				return isLt2M;
			},
			handleAvatarSuccess(res, file) { //图片上传成功
				this.imageUrl = URL.createObjectURL(file.raw);
				this.$message.success('上传成功');
				console.log(file.response.data)
				this.form.href = file.response.data;
			},
			handleExceed(files, fileList) { //图片上传超过数量限制
				this.$message.error('上传图片不能超过1张!');
			},
			submit() {
				var self = this;
				API.messageType(self.form).then(res => {
					self.$message.success('提交成功')
					self.dialogMessage = false;
					self.getMessageType();
					self.currentPage = 1;
				})
			},
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getMessages();
			},
		}
	}
</script>

<style scoped lang="scss">
	.editor-btn {
		margin-top: 30px;
	}
	
	.btn {
		margin-bottom: 1vw;
	}
	
	.el-table {
		border: 1px solid #dfe6ec;
	}
	
	.block {
		text-align: center;
		margin-top: 1vw;
	}
	
	.submit {
		margin-top: 1vw;
		text-align: right;
	}
</style>
